import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, parseDateMMDDYYYY } from "../../../stores/Utils";

import "./styles.css"

import { OpenModal } from "../../../components/DialogHRM";
import {  getall_collects	 } from "../services/server_fetching";

import {ViewComprobante_Cmpt } from "..";
import { TextInput_Cmpt } from "../../../components";

const ViewComprobante = ViewComprobante_Cmpt();

const TextInput = TextInput_Cmpt();

const frmNm = "amount2collect"

const EntryItem  = ({ data, elmId, openUpdateView }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);



	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}

	

	const refresh =  () => {
		getall_collects(updObs)
	}


	
	const openViewComprobante =  () => {
		updStore("show_comprobante", data);
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <ViewComprobante confirm={refresh} item={data}  accountForm={"Acc2Collect"} />
		OpenModal(modaldata);
	}


	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};
	


	
	return (
		<div className="item_acc">
			<div className="_dsplFlx operation_item_box" onClick={openViewComprobante}>
				
				<div className="name">
					{data?.document }
				</div>
				
				<div className="entryName">
					{data?.entryName }
				</div>
				<div className="flexSpace"/>
				<div className="code" >
					${(data?.amount*1).toFixed(2)} 
				</div>
				<div className="flexSpace"/>
			</div>
			
		</div>
	)
}
export default EntryItem;